import { mapState, mapMutations } from 'vuex';
import uniCard from '@dcloudio/uni-ui/lib/uni-card/uni-card.vue'
import uniSwiperDot from "@dcloudio/uni-ui/lib/uni-swiper-dot/uni-swiper-dot.vue"
import uniIcons from "@dcloudio/uni-ui/lib/uni-icons/uni-icons.vue"
import uniDrawer from '@dcloudio/uni-ui/lib/uni-drawer/uni-drawer.vue'
import cmdCelItem from "@/components/cmd-cell-item/cmd-cell-item.vue"
import cmdAvatar from "@/components/cmd-avatar/cmd-avatar.vue"
export default {
	computed: mapState(['forcedLogin', 'hasLogin', 'userName', 'serverUrl', 'user']),
	components:{
		uniCard,
		uniSwiperDot,
		uniIcons,
		uniDrawer,
		cmdCelItem,
		cmdAvatar
	},
	data() {
		return {
			entity:{},
			info: [{
									colorClass: 'uni-bg-red',
									url: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/shuijiao.jpg',
									content: '内容 A'
								},
								{
									colorClass: 'uni-bg-green',
									url: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/muwu.jpg',
									content: '内容 B'
								},
								{
									colorClass: 'uni-bg-blue',
									url: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg',
									content: '内容 C'
								}
			],
			dotStyle: [{
									backgroundColor: 'rgba(0, 0, 0, .3)',
									border: '1px rgba(0, 0, 0, .3) solid',
									color: '#fff',
									selectedBackgroundColor: 'rgba(0, 0, 0, .9)',
									selectedBorder: '1px rgba(0, 0, 0, .9) solid'
								},
								{
									backgroundColor: 'rgba(255, 90, 95,0.3)',
									border: '1px rgba(255, 90, 95,0.3) solid',
									color: '#fff',
									selectedBackgroundColor: 'rgba(255, 90, 95,0.9)',
									selectedBorder: '1px rgba(255, 90, 95,0.9) solid'
								},
								{
									backgroundColor: 'rgba(83, 200, 249,0.3)',
									border: '1px rgba(83, 200, 249,0.3) solid',
									color: '#fff',
									selectedBackgroundColor: 'rgba(83, 200, 249,0.9)',
									selectedBorder: '1px rgba(83, 200, 249,0.9) solid'
								}
							],
							modeIndex: -1,
							styleIndex: -1,
							current: 0,
							mode: 'default',
							dotsStyles: {},
			                imageArray:[],
							isShow:true,
							drawer:false
	}
	},
	methods: {
		seeOwner(){
			
			this.drawer=!this.drawer
			
		},
		 change(e) {
		            this.current = e.detail.current;
		        },
		 fnOrder(){
			 const that=this;
			 uni.showLoading({
			 	title:'提交中..'
			 })
			 let en={};
			 en.userid=that.user.id;
			 en.userpointid=that.entity.id;
			 en.ownerid=that.entity.tbUser.id;
			 en.goodsname=that.entity.goods.name;
			 en.goodsimage=that.entity.cargoImage;
			 en.goodsweight=that.entity.goods.weight;
			 en.goodsvolume=that.entity.goods.volume;
			 en.goodsstatus=that.entity.status;
			
			 uni.request({
			 		   	url: that.serverUrl + '/order/add', //仅为示例，并非真实接口地址。
			 		   	method: 'POST',
						data:en,
			 		   	header: {
			 		   		'content-type': 'application/json'
			 		   		//自定义请求头信息
			 		   	},
			 		   	success: res => {
			 //		   		console.log(res.data)
			 
			 		   		if(res.data.status){
											 
								uni.showToast({
								icon: 'none',
								title: '请求成功',
								
								complete() {
									
								
									
								}
								 });
								 
								 setTimeout(function() {
									 
									 uni.hideLoading();
									 uni.navigateBack({
									 	delta:1
									 })
									 
								 }, 1500);
								 
								
							}else{
								
								uni.showToast({
									icon: 'none',
									title: '请求失败',
									complete() {
										
									
										
									}
								});
								setTimeout(function() {
																	 
								 uni.hideLoading();
								 uni.navigateBack({
								 delta:1
								  })
																	 
								}, 1500);
								
								
							}
							
						
			 		   		
			 		   		},
			 		   	    fail: err => {
			 		   		uni.showToast({
			 		   			icon: 'none',
			 		   			title: '请求失败',
								
								
			 		   		});
			 		   		uni.hideLoading();
			 
			 		   	}
			 		   });
			 
			 
			 
			 
			 
		 },		
	},
	onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
	       const that=this
		   that.isShow=true;
	       uni.showLoading({
	       	title:'获取该物品详细中...'
	       })
		   let carId=option.id;
		   uni.request({
		   	url: that.serverUrl + '/user/userPoint/findOne/'+carId, //仅为示例，并非真实接口地址。
		   	method: 'POST',
		   	header: {
		   		'content-type': 'application/json'
		   		//自定义请求头信息
		   	},
		   	success: res => {
//		   		console.log(res.data)

		   		that.entity=res.data
				if(res.data.status==0){
					
					uni.showToast({
						icon:'none',
						title:'该包裹已被其他用户接单，暂时无法接单'
					})
					that.isShow=false;
					
				}
				let a=JSON.parse(res.data.cargoImage);
				a.forEach(p=>{
					let h={colorClass: 'uni-bg-red',url:that.serverUrl+'/'+p,content: '物品详情'}
					that.imageArray.push(h)
				})
		   		uni.hideLoading();

		   		},
		   	    fail: err => {
		   		uni.showToast({
		   			icon: 'none',
		   			title: '请求失败'
		   		});
		   		uni.hideLoading();

		   	}
		   });



	 }
}
